import React from "react";
import axios from "axios";
import SearchInput from "./SearchInput";
import ImageList from "./ImageList";
import img from "./image/ama.png";
class App extends React.Component {
  state = { images: [], userEntry: "" };

  onSearchSubmit = async (entry) => {
    console.log("user entry");
    console.log(entry);
    this.setState({ userEntry: entry });
    const response = await axios.get(
      `https://pixabay.com/api/?key=17790768-506e9450e951a7d477d743476&q=${entry}&image_type=photo`
    );
    console.log(response.data.hits);
    this.setState({ images: response.data.hits });
  };
  render() {
    return (
      <div className="ui container" style={{ marginTop: "150px" }}>
        <img className="picDisplay" src={img} />
        <SearchInput onSearchSubmit={this.onSearchSubmit} />
        <ImageList entry={this.state.userEntry} images={this.state.images} />
      </div>
    );
  }
}
export default App;
