import React from "react";
import "./image.css";
import img01 from "./image/HJ01.JPG";
import img02 from "./image/HJ02.jpg";
import img03 from "./image/HJ03.jpg";
import img04 from "./image/HJ04.JPG";
import img05 from "./image/HJ05.JPG";

const ImageList = (props) => {
  console.log("inside imagelist component");
  console.log(props.entry);
  const images = props.images.map((image) => {
    return (
      <img
        className="picDisplay"
        key={image.id}
        src={image.webformatURL}
        alt=""
      />
    );
  });

  const imagesList = [img01, img02, img03, img04, img05];
  const imageGallery = imagesList.map((imagesList) => {
    return <img className="picDisplay" src={imagesList} />;
  });

  console.log("image list component");
  console.log(props.state);

  const match = props.entry;
  console.log({ match });
  if (
    match.toLowerCase() == "hater jones" ||
    match.toLowerCase() == "haterjones"
  ) {
    return <div className="picGallery">{imageGallery}</div>;
  } else {
    return <div className="picGallery">{images}</div>;
  }
};

export default ImageList;
